<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  mounted() {
    this.$unlock();
  },
  data() {
    return {
      option: {
        title: {
          show: false
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          bottom: 10,
          left: "center",
          data: ["节日问候", "信息通知", "宣传推广"]
        },
        series: [
          {
            name: "发送类型统计",
            type: "pie",
            radius: "60%",
            center: ["50%", "50%"],
            data: [
              {
                name: "节日问候",
                value: Math.round(Math.random() * 100000)
              },
              {
                name: "信息通知",
                value: Math.round(Math.random() * 100000)
              },
              {
                name: "宣传推广",
                value: Math.round(Math.random() * 100000)
              }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      }
    };
  },
  components: {
    Echart
  }
};
</script>
<style lang="less" scoped>
.event-count {
  position: relative;

  > ul {
    position: absolute;
    display: flex;

    li {
      flex: 1;
    }
  }
}
</style>